.item {
    display: flex;
    width: 600px;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0px;
    padding: 0px 10px;
    background-color: #343a40;
    color: #fff;
    border-radius: 15px;
}

.item p {
    font-size: 1.3rem;
    max-width: 90%;
}

.item p::first-letter {
    text-transform: capitalize;
}

.item button {
    height: fit-content;
    padding: 5px 5px;
    line-height: 100%;
    border-radius: 7px;
    outline: none;
    border: 0;
}
