@media screen and (max-width: 768px) {
    .item {
        width: 85vw !important;
    }

    .tasks {
        padding: 15px 10px !important;
    }

    .form {
        width: 90vw !important;
        overflow: hidden;
        height: 60px !important;
        border-radius: 7px;
    }

    .formContainer {
        position: absolute;
        bottom: 25px;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100vw;
        overflow: hidden;
    }
}
